nav{
    width:100%;
    position:relative;
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content:center;
            justify-content:center;
    padding-top:1.5em;
    box-sizing:border-box;
    font-family:var(--font-main);
    letter-spacing:0.05em;
    -webkit-flex-basis:1000px;
            flex-basis:1000px;
    margin:0 auto;
    margin-bottom:1em;
  }

  nav::after{
    content:"";
    position:absolute;
    height:50%;
    width:100%;
    top:4em;
    background-image:url(/panchatt_website/static/media/navline.1e33ed13.svg);
    background-size:contain;
    background-position:center center;
    background-repeat:no-repeat;
  }


  nav>h1{
    font-size:2.5em;
    margin:0px;
    z-index:1;
    -webkit-flex-shrink:2;
            flex-shrink:2;
    text-transform:uppercase;
    display:grid;
    -webkit-align-items:flex-end;
            align-items:flex-end;
    margin-right:2.5em;
  }

  nav>ul{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-shrink:0;
            flex-shrink:0;
    list-style:none;
    padding:0px;
    z-index:1;
    font-weight:600;
    position:relative;
    top:3em;
  }

  nav>ul>li{
    margin:0px 0.5em;
    padding:0.2em 0.5em;
    font-size:1.3em;
    transition:-webkit-transform 0.1s ease-out;
    transition:transform 0.1s ease-out;
    transition:transform 0.1s ease-out, -webkit-transform 0.1s ease-out;
    cursor:pointer;
  }

  nav>ul>li:hover{
    -webkit-transform:scale(1.05);
            transform:scale(1.05);
    color:var(--red-primary);
  }

  nav>ul>li.special{
    color:white;
    border-radius:0.5em;
    padding:0px;
    position:relative;
  }
  nav>ul>li.special::before{
    z-index:-1;
    width:100%;
    height:100%;
    position:absolute;
    top:4px;
    left:0px;
    background-color:maroon;
    content:'';
    border-radius:inherit;
  }
  nav>ul>li.special>div{
    z-index:1;
    transition:-webkit-transform 0.2s ease-out;
    transition:transform 0.2s ease-out;
    transition:transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
    position:relative;
    background-color:#DA0000;
    width:100%;
    height:100%;
    border-radius:inherit;
    padding:0.2em 0.5em;
    box-sizing: border-box;
  }

  nav>ul>li.special:hover>div{
    -webkit-transform:translateY(2px);
            transform:translateY(2px);
  }

  nav>ul>li.special:hover{
    -webkit-transform:none;
            transform:none;
  }


  @media only screen and (max-width:920px){
    nav{
      width:100%;
      -webkit-justify-content:space-between;
              justify-content:space-between;
      background-color:white;
      padding:0.5em 2em;
      z-index:100;
    }
    nav::after{
      content:"";
      background-color:white;
      transition:box-shadow 0.4s ease-in;
      background-image:none;
      height:100%;
      top:0px;
      left:0px;
      box-shadow:0px 1px 2px rgba(0,0,0,0.3);
    }
    nav>ul{
      top:100%;
      z-index:-1;
      padding-top:1em;
      transition:-webkit-transform 0.2s ease-in-out;
      transition:transform 0.2s ease-in-out;
      transition:transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
      padding-left:1em;
      display:-webkit-flex;
      display:flex;
      -webkit-flex-flow:column;
              flex-flow:column;
      position:absolute;
      background-color:#fafafa;
      color:black;
      width:100%;
      margin:0px;
      box-sizing: border-box;
      left:0px;
      -webkit-align-items:flex-start;
              align-items:flex-start;
      padding-bottom:1em;
      box-shadow:0px 2px 4px rgba(0,0,0,0.3);
    }
    nav[collapse="1"]::after{
      box-shadow:none;
    }
    nav>ul>li{
      -webkit-flex-grow:0;
              flex-grow:0;
      margin-bottom:0.5em;
    }
    nav>h1{
      -webkit-align-self:center;
              align-self:center;
      font-size:1.8em;
      z-index:2;
      background-color:white;
    }
    nav>div.burger{
      z-index:2;
      position:relative;
      width:2em;
      height:2em;
      display:-webkit-flex;
      display:flex;
      -webkit-flex-flow:column;
              flex-flow:column;
      -webkit-justify-content:space-around;
              justify-content:space-around;
      -webkit-align-self:center;
              align-self:center;
    }
    nav>div.burger>span{
      background-color:black;
      -webkit-flex-shrink:0;
              flex-shrink:0;
      width:100%;
      height:4px;
      z-index:2;
      transition:opacity 0.2s ease-out, -webkit-transform 0.2s ease-in;
      transition:transform 0.2s ease-in, opacity 0.2s ease-out;
      transition:transform 0.2s ease-in, opacity 0.2s ease-out, -webkit-transform 0.2s ease-in;
    }
    nav>div.burger>span:nth-child(2){
      -webkit-transform-origin:100% 50%;
              transform-origin:100% 50%;
    }
    nav>div.burger:hover>span:first-child{
      -webkit-transform:rotateZ(-10deg);
              transform:rotateZ(-10deg);
    }
    nav>div.burger:hover>span:nth-child(2){
      -webkit-transform:scaleX(0.8);
              transform:scaleX(0.8);
    }
    nav>div.burger:hover>span:last-child{
      -webkit-transform:rotateZ(10deg);
              transform:rotateZ(10deg);
    }
    nav[collapse="1"]>div.burger>span:first-child{
      -webkit-transform:translateY(250%) rotateZ(45deg);
              transform:translateY(250%) rotateZ(45deg);
      background-color:var(--red-primary);
    }
    nav[collapse="1"]>div.burger>span:nth-child(even){
      -webkit-transform:scaleX(0);
              transform:scaleX(0);
    }
    nav[collapse="1"]>div.burger>span:last-child{
      -webkit-transform:translateY(-250%) rotateZ(-45deg);
              transform:translateY(-250%) rotateZ(-45deg);
      background-color:var(--red-primary);
    }
    nav[collapse="0"]>ul{
      -webkit-transform:translateY(-100%);
              transform:translateY(-100%);
    }
    nav>div.burger>span:nth-child(2){
      background-color:var(--red-primary);
    }

  }

