}

div.train-assembly>div.platform-front{
    position:absolute;
    top:630px;
    background-image:url(/panchatt_website/static/media/platform_front.818c7fe9.svg);
    background-size:contain;
    background-repeat:no-repeat;
    height:411px;
    width:calc(411 * calc(1440 / 550));
    width:150%;
}

div.train-assembly>div.platform-front::before, div.train-assembly>div.platform-front::after{
    width:3500px;
    position:absolute;
    left:1000px;
    content:"";
    height:calc(100% - 3px);
    top:3px;
    background-color:#c5c4b5;
}

div.train-assembly>div.platform-front::after{
    height:42%;
    background-color:#adac9e;
}

div.train-assembly>div.train{
    --coach-count:4;
    width:calc(calc(1 + var(--coach-count)) * 1050px);
    height:400px;
    position:absolute;
    display:-webkit-flex;
    display:flex;
    /* opacity:0; */
    /* left:calc(100vw - 30%); */
    left:100vw;
    top:280px;
}

div.train>div.engine{
    width:970px;
    height:400px;
    position:relative;
    top:10px;
    background-size:cover;
    background-image:url(/panchatt_website/static/media/engine_main.cc9e32ed.svg);
}

div.train>div.coach{
    width:1050px;
    height:400px;
    background-size:contain;
    background-position:bottom center;
    background-repeat: no-repeat;
    position:relative;
    top:-10px;
    background-image:url(/panchatt_website/static/media/coach_empty.22b64623.svg);
}

div.train>div.coach>p{
    font-size:1.2em;
    margin:0px;
    left:280px;
    top:235px;
    box-sizing:border-box;
    z-index:2;
    padding:0.5em;
    width:480px;
    height:103px;
    font-family:'Milletun', sans-serif;
    letter-spacing:0.08em;
    display:grid;
    place-items:center;
    text-align:center;
    position:absolute;
}


div.train[stage="0"]{
    -webkit-transform:translateX(-291px);
            transform:translateX(-291px);
}

div.train[stage="1"]{
    -webkit-animation:first-coach-entry 4s ease-in-out forwards;
            animation:first-coach-entry 4s ease-in-out forwards;
}

div.train[stage="2"]{
    -webkit-animation:second-coach-entry 4s ease-in-out forwards;
            animation:second-coach-entry 4s ease-in-out forwards;
}

div.train[stage="3"]{
    -webkit-animation:third-coach-entry 4s ease-in-out forwards;
            animation:third-coach-entry 4s ease-in-out forwards;
}


div.train[stage="4"]{
    -webkit-animation:fourth-coach-entry 4s ease-in-out forwards;
            animation:fourth-coach-entry 4s ease-in-out forwards;
}


div.train[stage="5"]{
    -webkit-animation:fifth-coach-entry 4s ease-in-out forwards;
            animation:fifth-coach-entry 4s ease-in-out forwards;
}





@-webkit-keyframes first-coach-entry{
    from{
        -webkit-transform:translateX(0px);
                transform:translateX(0px);
    }
    to{
        -webkit-transform:translateX(calc(-1 * calc(970px + 50vw + calc(0.5 * 1050px * 1))));
                transform:translateX(calc(-1 * calc(970px + 50vw + calc(0.5 * 1050px * 1))));
    }
}





