#panchatt_shadow{
    transform-box:fill-box;
    -webkit-transform-origin:100% 100%;
            transform-origin:100% 100%;
    transition:-webkit-transform 0.5s ease-in;
    transition:transform 0.5s ease-in;
    transition:transform 0.5s ease-in, -webkit-transform 0.5s ease-in;
    -webkit-animation:oscillate_shadow 2s ease-in-out alternate infinite;
            animation:oscillate_shadow 2s ease-in-out alternate infinite;
}
@-webkit-keyframes oscillate_shadow{
    from{
        -webkit-transform:translateX(-5%) rotateZ(20deg);
                transform:translateX(-5%) rotateZ(20deg);
    }
    to{
        -webkit-transform:translateX(-5%) rotateZ(90deg);
                transform:translateX(-5%) rotateZ(90deg);
    }
}
@keyframes oscillate_shadow{
    from{
        -webkit-transform:translateX(-5%) rotateZ(20deg);
                transform:translateX(-5%) rotateZ(20deg);
    }
    to{
        -webkit-transform:translateX(-5%) rotateZ(90deg);
                transform:translateX(-5%) rotateZ(90deg);
    }
}
